$blue-water: #00b7ff;
$light-grey: #d3d3d3;
$red: #fc160e;
$cream: #fffdd0;
$white: #ffffff;

@keyframes bump {
  0% {
    transform: scale(1);
  }
  10% {
    transform: scale(0.9);
  }
  30% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1.15);
  }
  100% {
    transform: scale(1);
  }
}
